@import url(https://fonts.googleapis.com/css2?family=Outfit&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  transition: all 0.15s;
}

body {
  background-color: #0D1116;
  color: #C9D1D9;
  margin: 0;
  font-family: 'Outfit', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  color: #C9D1D9;
  text-decoration: none;
}

main {
  padding: 2rem;
}

/* Mobile */
@media only screen and (max-width: 600px) {
  main {
    min-height: calc(100vh);
    min-width: calc(100vw);
  }
}

/* Web or other */
@media only screen and (min-width: 601px) {
  main {
    min-height: 100vh;
    min-width: calc(100vw - 16rem);
  }
}

.App {
  display: flex;
}

/* Mobile */
@media only screen and (max-width: 760px) {
  a {
    padding: 4vw 0;
    width: calc(20%);
    text-align: center;
  }

  a:first-child {
    padding-left: 2vw;
    width: calc(20% + 2vw);
  }

  a:nth-child(5) {
    padding-right: 2vw;
    width: calc(20% + 2vw);
  }

  .active {
    color: #9c90e8;
  }

  .icon {
    font-size: 6vw;
  }

  .label {
    font-size: 3vw;
  }
}

/* Web or other */
@media only screen and (min-width: 761px) {
  a {
    display: flex;
    font-size: 1.2rem;
    padding: 1rem;
    text-decoration: none;
  }

  a:hover {
    color: #9C90E8;
  }

  .active {
    background-color: #30363D;
  }

  .icon {
    margin: auto 0.1rem auto 0;
  }

  .label {
    padding-left: 0.5rem;
  }
}

nav {
  background-color: #161B22;
}

/* Mobile */
@media only screen and (max-width: 760px) {
  nav {
    border-top: 2px solid #30363D;
    bottom: 0;
    display: flex;
    min-width: 100vw;
    position: fixed;
  }

  .web {
    display: none;
  }

  .divider, .info {
    display: none;
  }
}

/* Web or other */
@media only screen and (min-width: 761px) {
  nav {
    border-right: 2px solid #484F58;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    padding-bottom: 1rem;
    width: 16rem;
  }

  .mobile {
    display: none;
  }

  .divider {
    flex-grow: 1;
  }

  .info {
    text-align: center;
  }
}

